<script>
    import { Swipe, SwipeItem } from "svelte-swipe";
    let active_item = 1; //readonly
    let SwipeComp;
  
      function changeSlide(i){
      SwipeComp.goTo(i)
    }
  
    export let images = [];

</script>

<style>

  .swipe-holder{
    width: 100%;
    height: 90vw;
  }
  .card-footer{
    justify-content: center;
    align-items: center;
  }
  img.thumb{
    height:60px; 
    width:60px; 
    cursor:pointer;
    object-fit: cover;
    object-position: center;
  }
  img.featured {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
    background-color: rgb(65, 64, 64);
  }
  @media(min-width:769px){ 
    .swipe-holder{
    height: 30vw;
  }

  }
  
</style>

<div class="card">
  <div class="swipe-holder card-image">
      <Swipe bind:active_item bind:this={SwipeComp}>
          {#each images as image}
          <SwipeItem>
              <img src={image} alt="" class="featured">
          </SwipeItem>
          {/each}
      </Swipe>
  </div>
  <div class="card-footer">
    {#each images as image, i}
      <img class="{active_item == i ? 'rounded' : 'img-thumbnail'} thumb" on:click={() => changeSlide(i)}  src={image} alt="">
    {/each}
  </div>
</div>

