<script>
import { onMount } from 'svelte';
import Gallery from "../common/Gallery.svelte";

export let id;

let body = `query MyQuery {
    work(filter: {id: {eq: "`+ id +`"}}) {
    title
    images {
        url
    }
    materials{
        name
        description
    }
    survey {
        datetime
        location {
        coordinates {
            latitude
            longitude
        }
        name
        }
        datetime
    }
    workType {
        name
    }
    }
}
`;
    let token = "4453e2d25a25172b02202e78a1e78e";
    const fetchImage = (async () => {
        const response = await fetch(
        'https://graphql.datocms.com/',
        {
            method: 'POST',
            headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': `Bearer ${token}`,
            },
            body: JSON.stringify({
            query: body
            }),
    })
        let responsetxt = await response.json()
        console.log(responsetxt)
        const work =  { 
            'date': formatDate(responsetxt.data.work.survey.datetime),
            'locationName' : responsetxt.data.work.survey.location.name,
            'latitude' : responsetxt.data.work.survey.location.coordinates.latitude,
            'longitude' : responsetxt.data.work.survey.location.coordinates.longitude,
            'mapUrl' : "https://api.mapbox.com/styles/v1/mapbox/satellite-streets-v11/static/pin-s+555555(" 
                + responsetxt.data.work.survey.location.coordinates.longitude
                + ","
                + responsetxt.data.work.survey.location.coordinates.latitude
                + "/"
                + responsetxt.data.work.survey.location.coordinates.longitude
                + ","
                + responsetxt.data.work.survey.location.coordinates.latitude
                + ",6,0/200x200?access_token=pk.eyJ1IjoiY3JhaWctY2hhbWJlcmxhaW4iLCJhIjoiY2ttbTByYmx3MWdiZzJvazV0Zm55ZHBpciJ9.pmN8DDzUEXNr8JH1gXN7Cg",
            'materials' : responsetxt.data.work.materials,
            'title' : responsetxt.data.work.title,
            'images' : responsetxt.data.work.images.map(image => image.url)
        }
        console.log(work)
        return work
    })();
    function formatDate(dateString){
        const date = new Date(dateString)
        return date.toDateString()
    }
    
</script>
<style>
    @media(min-width:570px){
        li {
            display: flex;
            justify-content: space-between;
        }
    }
</style>
    
<section class="section">
{#await fetchImage then work}        
    <h1 class="title has-text-centered">{work.title}</h1>
    <div class="columns">
        <div class="column is-half">
            <Gallery images={work.images}/>
         </div>
        <div class="column is-half">
            <ul>
            <li><h1>Location:</h1> {work.locationName}</li>
            <li><h1>Map:</h1><img src="{work.mapUrl}" alt="map">
            </li>
            <li>
                <h1>Coordinates:</h1> 
                <span class="longitude">{work.longitude}</span>,
                <span class="latitude">{work.latitude}</span>
            </li>
            
            <li><h1>Date:</h1> {work.date}</li>
            <li><h1>Material:</h1> 
                <ul>
                    <dl>
                           {#each work.materials as material}
                           <dl>
                               <dt>{material.name}</dt>
                               <dd>{material.description}</dd>
                           </dl>
                           {/each} 
                    </dl>
                </ul>
            </li>
            </ul>
        </div>

    </div>
{/await}
</section>